import React, { useLayoutEffect } from "react";
import Comment from "./comment";
import useArticle from "../hooks/useArticle";
import { os } from "../components/footer";
function Article({ match }: any) {
  const { articletState, fetchArticle } = useArticle();
  let comProps = {
    aTitle: "dororodoroddo",
    page: match.params.page,
    id: match.params.id,
  };
  useLayoutEffect(() => {
    if (
      !articletState.has(match.params.page) ||
      !articletState.get(match.params.page).has(match.params.id)
    ) {
      fetchArticle(match.params.page, match.params.id);
      if (os) {
        os.article(match.params.page, match.params.id);
      } else {
        setTimeout(() => os.article(match.params.page, match.params.id), 200);
      }
    } else {
      document.querySelector("#title")!.innerHTML = articletState
        .get(match.params.page)
        .get(match.params.id)["title"];
      comProps.aTitle = articletState
        .get(match.params.page)
        .get(match.params.id)["title"];
      document.querySelector("#date")!.innerHTML = articletState
        .get(match.params.page)
        .get(match.params.id)["date"];
      document.querySelector("#content")!.innerHTML = articletState
        .get(match.params.page)
        .get(match.params.id)["content"];
    }
  });
  return (
    <>
      <h1>
        <span id="title"></span>
      </h1>
      <h3 className="sub">
        <i className="fas fa-user-alt"></i> 도로로도로또
        <i className="far fa-calendar-alt"></i> <span id="date"></span>
      </h3>
      <hr />
      <div id="content">
        
      </div>
      <hr />
      <Comment comProps={comProps} />
    </>
  );
}

export default Article;
