import React, { Component } from "react";
import "../css/Navi.scss";
import "../os.ts";
import { Link } from "react-router-dom";

let a = setTimeout(() => {}, 10);
class Navi extends Component {
  componentDidMount() {
    document.querySelector(".hamburg")!.addEventListener("click", () => {
      document.querySelector(".nav-items")!.classList.toggle("active");
      document.querySelector(".hamburg")!.classList.toggle("active");
      clearTimeout(a);
      a = setTimeout(() => {
        document.querySelector(".nav-items")!.classList.remove("active");
        document.querySelector(".hamburg")!.classList.remove("active");
      }, 5000);
      document.querySelector(".nav-items")!.addEventListener("click", () => {
        document.querySelector(".nav-items")!.classList.remove("active");
        document.querySelector(".hamburg")!.classList.remove("active");
      });
    });
  }
  render() {
    return (
      <nav>
        <h1>
          <Link className="nav-brand" to="/">
            dororodoroddo
          </Link>
        </h1>
        <div className="nav-items">
          <Link to="/common/" className="nav-a">
            일반
          </Link>
          <Link to="/develop/" className="nav-b">
            개발
          </Link>
          <Link to="/etcs/" className="nav-c">
            기타
          </Link>
        </div>
        <div className="hamburg">
          <i className="fas fa-plus"></i>
        </div>
      </nav>
    );
  }
}

export default Navi;
