import React, { useLayoutEffect } from "react";
import OS from "../os";
import useArticle from "../hooks/useArticle";
import useArticleList from "../hooks/useArticleList";

export let os: any;
function Foot({ match }: any) {
  const { fetchArticle } = useArticle();
  const { fetchArtiList } = useArticleList();
  useLayoutEffect(() => {
    fetchArtiList("common");
    fetchArtiList("develop");
    fetchArtiList("react");
    fetchArtiList("etcs");
    fetchArticle("common", "0");
    fetchArticle("develop", "0");
    fetchArticle("react", "0");
    fetchArticle("etcs", "0");
    os = new OS({ fetchArticle, fetchArtiList });
    // eslint-disable-next-line
  }, []);

  return (
    <footer>
      Copyright ©<a href="https://github.com/dororodoroddo">도로로도로또</a>
      &nbsp; All rights reserved.
    </footer>
  );
}
export default Foot;
