import React, { useLayoutEffect } from "react";
import { Link } from "react-router-dom";

import useArticleList from "../hooks/useArticleList";
// import useInner from '../hooks/useInner'

function List({ data, page }: any) {
  return (
    <li>
      <Link to={`/${page}/${data.id}/`}>{data.title}</Link>
    </li>
  );
}

function Sidebar({ page }: any) {
  const { artiListState } = useArticleList();
  // const { innerState, controlPage } = useInner();
  useLayoutEffect(() => {

    return () => {};
  });
  return (
    <>
      <strong className="artiListHeader">
        <span>글 목록</span>
        <i className="fas fa-sort-down"></i>
      </strong>
      <ul className="artiList">
        {page in artiListState ? (
          artiListState[page].length === 0 ? (
            <li>게시글이 없어요</li>
          ) : (
            artiListState[page].map((arti: any) => (
              <List data={arti} page={page} key={arti.id} />
            ))
          )
        ) : null}
      </ul>
    </>
  );
}

export default Sidebar;
