import React, { useLayoutEffect } from "react";
import { Route } from "react-router-dom";
import Article from "../components/article";
import Main from "../components/main";
import Sidebar from "../components/sidebar";
import "../css/article.scss";
function hide(): void {
  document.querySelector(".artiList")?.classList.toggle("active");
}
let pageSelector: string = "a";
function ArticlePage({ match }: any) {
  useLayoutEffect(() => {
    if (match.params.page === "common") {
      pageSelector = "a";
    } else if (match.params.page === "develop") {
      pageSelector = "b";
    } else if (match.params.page === "etcs") {
      pageSelector = "c";
    } else {
      pageSelector = "";
    }
    document.querySelector(`.nav-${pageSelector}`)?.classList.add("active");
    document.querySelector(".artiListHeader")?.addEventListener("click", hide);
    console.log();

    return () => {
      document
        .querySelector(`.nav-${pageSelector}`)
        ?.classList.remove("active");
    };
  });
  return (
    <main>
      {window.location.href.split("/")[4] ? (
        <aside>
          <Sidebar page={match.params.page} />
        </aside>
      ) : null}
      <article>
        <Route path="/:page/" exact component={Main} />
        <Route path="/:page/:id/" exact component={Article} />
      </article>
      <div className="margin"></div>
    </main>
  );
}

export default ArticlePage;
