interface fetchs {
  fetchArticle: any;
  fetchArtiList: any;
}

class os {
  fetchArticle: any;
  fetchArtiList: any;
  artis: Map<string, Set<string>>;
  constructor({ fetchArticle, fetchArtiList }: fetchs) {
    this.fetchArticle = fetchArticle;
    this.fetchArtiList = fetchArtiList;
    this.artis = new Map();
  }
  article(page: string, id: string) {
    if (!this.artis.has(page)) {
      this.artis.set(page, new Set());
    }
    this.artis.get(page)!.add(id);

    if (!this.artis.get(page)!.has(String(parseInt(id) + 1))) {
      this.fetchArticle(page, String(parseInt(id) + 1));
    }
    if (!this.artis.get(page)!.has(String(parseInt(id) - 1))) {
      this.fetchArticle(page, String(parseInt(id) - 1));
    }
  }
}

export default os;
